import "mapbox-gl/dist/mapbox-gl.css";
import React, { useState, useEffect, useCallback } from "react";
import axios from "axios";

import Splash from "./components/Splash";
import Trip from "./components/Trip";
import "./css/app.css";

const fetchData = (FilE_NAME) => {
  const res = axios.get(
    `https://raw.githubusercontent.com/1023sherry/kickboard/main/kickboard/src/data/${FilE_NAME}.json`
  );
  const data = res.then((r) => r.data);
  return data;
};

const App = () => {
  const [icon, setIcon] = useState([]);
  const [kick_OO, setKick_OO] = useState([]);
  const [kick_DD, setKick_DD] = useState([]);
  const [pt_OD, setPt_OD] = useState([]);
  const [people, setPeople] = useState([]);

  const [isloaded, setIsLoaded] = useState(false);

  const getData = useCallback(async () => {

    const KICK_OO = await fetchData("kick_OO_40");
    const KICK_DD = await fetchData("kick_DD_40");
    const PT_OD = await fetchData("pt_OD_40");
    
    const PEOPLE = await fetchData("people_location");

    
    const ICON = await Promise.all([
      fetchData("icon"),
      fetchData("trail_icon"),
    ]);

    setIcon((prev) => ICON.flat());
    setKick_OO((prev) => KICK_OO);
    setKick_DD((prev) => KICK_DD);
    setPt_OD((prev) => PT_OD);
    setPeople((prev) => PEOPLE);
 

    setIsLoaded(true);
  }, []);

  useEffect(() => {
    getData();
  }, [getData]);

  return (
    <div className="container">
      {!isloaded && <Splash />}
      {isloaded && (
        <Trip 
              kick_OO={kick_OO}
              pt_OD={pt_OD}
              kick_DD={kick_DD}
              icon={icon}
              people={people}
              />
      )}
    </div>
  );
};

export default App;
